<template>
  <div v-resize="onResize">
    <v-card
      :color="$vuetify.theme.dark ? 'dark' : 'dark'"
      class="rounded-lg mx-auto elevation-3"
      outlined
      v-show="!this.datakaryawan.visible"
    >
      <v-sheet color="transparent" flat height="130">
        <v-list-item three-line>         
          <v-list-item-content>
            <div class="title text-truncate">Karyawan</div>
            <!-- <v-list-item-title class="title mb-1">Jabatan</v-list-item-title> -->

            <div class="no-gutters d-inline-block text-truncate">
              <span v-text="datakaryawan.Nama"></span>
            </div>

            <div class="no-gutters d-inline-block text-truncate">
              <span v-text="datakaryawan.NRK"></span>
            </div>

            <div class="no-gutters d-inline-block text-truncate">
              <span v-text="datakaryawan.Lokasi"></span>
            </div>

            <!-- <v-list-item-title class="title mb-2">
              Karyawan
            </v-list-item-title>
            
            <div class="d-inline-block text-truncate mb-1">
              <label v-text="datakaryawan.Nama"></label>
            </div>
            <div class="d-inline-block text-truncate">
              <label v-text="datakaryawan.NRK"></label>
            </div>
            <div class="d-inline-block text-truncate">
              <label v-text="datakaryawan.Lokasi"></label>
            </div> -->
          </v-list-item-content>

          <v-hover v-slot="{ hover }">
            <v-list-item-avatar
              :class="{ 'on-hover': hover }"
              size="55" 
              color="grey"
              outlined
              class="mb-n8 mx-auto rounded-lg"
            >
              <v-responsive :aspect-ratio="1/1">
              <v-img
                :src="foto"
                gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.3)"
              >
                <!-- Dialog Lihat Foto -->
                <v-dialog
                  v-model="DialogLihatFoto"
                  max-width="300"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <div class="align-self-center mx-auto">
                      <v-btn
                        text
                        height="60"
                        width="60"
                        large
                        class="mx-n3 rounded-lg"
                        :class="{ 'show-btns': hover }"
                        :color="transparent"
                        v-bind="attrs"
                        v-on="on"
                      >
                        <v-icon
                          :class="{ 'show-btns': hover }"
                          :color="transparent"
                        >
                          mdi-eye-outline
                        </v-icon>

                        <!-- <v-sheet outlined class="rounded-pill">
                          <span class="text-capitalize">Lihat</span>
                        </v-sheet> -->
                      </v-btn>
                    </div>
                  </template>
                  
                  <!-- Foto -->
                  <v-card outlined class="pa-1">
                    <v-card
                      outlined
                      color="grey lighten-2"
                      class="fill-height"
                    >
                      <v-responsive :aspect-ratio="3/4">
                        <v-img
                          v-if="foto"
                          :src="foto"
                          class="d-flex flex-row fill-height"
                        >
                          <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn
                              fab
                              x-small
                              @click="DialogLihatFoto = false"
                            >
                              <v-icon>mdi-close-thick</v-icon>
                            </v-btn>
                          </v-card-actions>

                          <template v-slot:placeholder>
                            <v-row
                              class="fill-height ma-0"
                              align="center"
                              justify="center"
                            >
                              <v-progress-circular
                                indeterminate
                                color="grey lighten-5"
                              ></v-progress-circular>
                            </v-row>
                          </template>
                        </v-img>
                      </v-responsive>  
                    </v-card>
                  </v-card>
                </v-dialog>
              </v-img>
              </v-responsive>
            </v-list-item-avatar>
          </v-hover>
        </v-list-item>
      </v-sheet>

      <!-- Dialog Detail Karyawan -->
      <v-card-actions>
        <v-dialog
          v-model="dialog"
          scrollable
          max-width="900"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              text
              small
              outlined
              v-bind="attrs"
              v-on="on"
              class="text-capitalize subtitle-1 rounded-lg"
            >
              <v-icon large>mdi-dots-horizontal</v-icon>
            </v-btn>
          </template>

          <v-card outlined class="rounded-lg">
            <v-toolbar flat height="64">
              <v-toolbar-title class="title">                
                <span class="text-h5 mx-1">Detail Karyawan</span>
              </v-toolbar-title>
              <v-spacer></v-spacer>
              <v-btn
                icon
                @click="dialog = false"
              >
                <v-icon>mdi-window-close</v-icon>
              </v-btn>
            </v-toolbar>
            <v-divider></v-divider>
            <v-card-text>
              <!-- Mode Desktop -->
              <div class="mr-n3 ml-n3 mb-n2 mt-3" v-show="mobile == false">
                <v-sheet
                  outlined
                  class="rounded-lg pa-2"
                >
                  <v-row>
                    <v-col cols="10" sm="9" md="9" lg="9">
                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Nama</label>
                        </v-col>
                        <label class="subtitle-1 col-9">: <span v-text="datakaryawan.Nama"></span></label> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">NRK</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.NRK"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">POH</label>
                        </v-col>
                        <label class="subtitle-1 col-9">: <span v-text="datakaryawan.POH"></span></label> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Lokasi</label>
                        </v-col>
                        <label class="subtitle-1 col-9">: <span v-text="datakaryawan.Lokasi"></span></label> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Cabang</label>
                        </v-col>
                        <label class="subtitle-1 col-9">: <span v-text="datakaryawan.Cabang"></span></label> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Tanggal Masuk</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.TglMasuk"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Masa Kerja</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="MasaKerja+ ' Hari'"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Tempat Lahir</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.TempatLahir"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Tanggal Lahir</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.TglLahir"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Alamat KTP</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.AlamatKTP"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Alamat Sekarang</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.AlamatSekarang"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">No. Telp</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.NoTlp"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Jenis Kelamin</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.JenisKelamin"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Pendidikan Terakhir</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.PendidikanTerakhir"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Jurusan</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.Jurusan"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Agama</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.Agama"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Suku</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.Suku"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">No. Rek. Tabungan</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.NoRekTabungan"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">No. Kartu BPJS TK</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.NoKartuBPJSTK"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">No. Kartu BPJS KES</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.NoKartuBPJSKes"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">No. KTP</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.NoKTP"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">No. NPWP</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.NoNPWP"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Golongan Darah</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.GolDarah"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Status</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.StatusPerkawinan"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Status Kerja</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.StatusKerja"></span></span> 
                      </div>
                      
                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Ukuran Baju</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.UkuranBaju"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Ukuran Sepatu</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.UkuranSepatu"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">PHK</label>
                        </v-col>
                        <span class="subtitle-1 col-4">: <span v-text="datakaryawan.PHK"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Tanggal PHK</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.TglPHK"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Keterangan</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.Keterangan"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Nama Istri Suami</label>
                        </v-col>
                        <span class="subtitle-1 col-9">: <span v-text="datakaryawan.NamaIstriSuami"></span></span> 
                      </div>

                      <div class="d-flex flex-row mb-n5">
                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Anak</label>
                        </v-col>
                        <div class="ma-3">
                          <span class="subtitle-1">:
                            <span class="mr-2" v-text="jmlanak"></span>
                            
                          </span> 
                          <v-dialog
                            v-model="DialogLihatDetailAnakModeDesktop"
                            scrollable
                            max-width="500px"
                          >
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn
                                dark
                                small
                                depressed
                                v-bind="attrs"
                                v-on="on"
                                class="text-capitalize"
                                color="red darken-4"
                              >
                                <span>Detail</span>
                              </v-btn>
                            </template>
                            <v-card
                              outlined
                              class="rounded-lg"
                            >
                              <v-card-title>
                                Detail Anak
                              </v-card-title>
                              <v-divider></v-divider>
                              <v-card-text>
                                <div class="mb-n2 mt-3 mr-n3 ml-n3">  
                                  <v-card
                                    outlined
                                    class="rounded-lg"
                                  >
                                    <v-simple-table>
                                      <template v-slot:default>
                                        <thead>
                                          <tr>
                                            <th class="text-left subtitle-1" width="100">
                                              Anak Ke
                                            </th>
                                            <th class="text-left subtitle-1">
                                              Nama Anak
                                            </th>
                                          </tr>
                                        </thead>
                                        <tbody>
                                          <tr
                                            v-for="(Anak,index) in anak"
                                            :key="index"
                                          >
                                            <td class="subtitle-1" v-text="Anak.AnakKe"></td>
                                            <td class="subtitle-1" v-text="Anak.NamaAnak"></td>
                                          </tr>
                                        </tbody>
                                      </template>
                                    </v-simple-table>
                                  </v-card>
                                </div>
                              </v-card-text>
                            </v-card>
                          </v-dialog>
                        </div>
                      </div>

