                        <v-col cols="6" sm="5" md="5" lg="5">
                          <label class="subtitle-1">Vaksin</label>
                        </v-col>

                        <div class="ma-3">
                          <span class="subtitle-1">:
                            <span class="mr-4 "></span>
                            
                          </span> 
                          <v-dialog
                            v-model="DialogLihatDetailVaksin"
                            scrollable
                            max-width="500px"
                          >
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn
                                dark
                                small
                                depressed
                                v-bind="attrs"
                                v-on="on"
                                class="text-capitalize"
                                color="red darken-4"
                              >
                                Detail
                              </v-btn>
                            </template>
                            <v-card
                              outlined
                              class="rounded-lg"
                            >
                              <v-toolbar flat>
                                <v-toolbar-title class="title">
                                  Detail Vaksin
                                </v-toolbar-title>
                                <v-spacer></v-spacer>
                                <v-btn fab text small @click="DialogLihatDetailVaksin = false">
                                  <v-icon>mdi-close</v-icon>
                                </v-btn>
                              </v-toolbar>
                              <v-divider></v-divider>
                              <v-card-text>
                                <div
                                  v-for="n in 2"
                                  :key="n"
                                  class="mt-4 mb-n1 mr-n2 ml-n2"
                                >
                                  <v-card tile class="pa-1 elevation-3" outlined>
                                    <v-card
                                      tile
                                      outlined
                                      class="pa-3"
                                    >
                                      <v-row dense no-gutters>
                                        <div class="col-6">
                                          <span class="subtitle-1">Vaksin Ke</span>
                                        </div>
                                        <div>
                                          <span class="subtitle-1">:
                                            <span class="subtitle-1">1</span>
                                          </span>
                                        </div>
                                      </v-row>

                                      <v-row dense no-gutters>
                                        <div class="col-6">
                                          <span class="subtitle-1">Tanggal Vaksin</span>
                                        </div>
                                        <div>
                                          <span class="subtitle-1">:
                                            <span class="subtitle-1">2021-08-14</span>
                                          </span>
                                        </div>
                                      </v-row>

                                      <v-row dense no-gutters>
                                        <div class="col-6">
                                          <span class="subtitle-1">Keterangan</span>
                                        </div>
                                        <div>
                                          <span class="subtitle-1">:
                                            <span class="subtitle-1">-</span>
                                          </span>
                                        </div>
                                      </v-row>

                                      <v-row dense no-gutters>
                                        <div class="col-6">
                                          <span class="subtitle-1">Sertifikat</span>
                                        </div>
                                        <div>
                                          <span class="subtitle-1">:
                                            <v-dialog
                                              v-model="DialogLihatSertifikatVaksin"
                                              persistent
                                              max-width="500"
                                              :hide-overlay = "true"
                                            >
                                              <template v-slot:activator="{ on, attrs }">
                                                <v-btn
