                                                  Lihat
                                                </v-btn>
                                              </template>
                                              <v-card outlined class="pa-1">
                                                <v-card
                                                  outlined
                                                  color="grey lighten-2"
                                                  class="fill-height"
                                                >
                                                  <v-responsive :aspect-ratio="4/3">
                                                    <v-img
                                                      v-if="foto"
                                                      :src="foto"
                                                      class="d-flex flex-row fill-height"
                                                    >
                                                      <v-card-actions>
                                                        <v-spacer></v-spacer>
                                                        <v-btn
                                                          fab
                                                          x-small
                                                          @click="DialogLihatSertifikatVaksin = false"
                                                        >
                                                          <v-icon>mdi-close-thick</v-icon>
                                                        </v-btn>
                                                      </v-card-actions>

                                                      <template v-slot:placeholder>
                                                        <v-row
                                                          class="fill-height ma-0"
                                                          align="center"
                                                          justify="center"
                                                        >
                                                          <v-progress-circular
                                                            indeterminate
                                                            color="grey lighten-5"
                                                          ></v-progress-circular>
                                                        </v-row>
                                                      </template>
                                                    </v-img>
                                                  </v-responsive>  
                                                </v-card>
                                              </v-card>
                                            </v-dialog>
